import React from 'react';
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { translate } from 'react-i18next';

import OffsetOrder from '../../components/mortgageOrder/offsetOrder';
import Login from '../../components/login/login';
import MortgageUser from '../../components/mortgageUser/mortgageUser';
import Header from '../../components/header/header';
import Footer from '../../components/footer/footer';
import WithPopup from '../../components/withPopup/withPopup';
import Prompt from '../../components/prompt/prompt';

import * as bookActions from '../../actions/book';
import * as userActions from '../../actions/user';
import * as modalActions from '../../actions/modal';
import * as mortgageActions from '../../actions/mortgage';

import connectFirebase from '../../hoc/connectFirebase';
import '../mortgage/mortgage.scss';

const mapStateToProps = (state) =>
{
    return {
        from: state.book.from,
        to: state.book.to,
        user: state.user.user,
        account: state.user.account,
        tokenList: state.book.tokenList,
        loading: state.modal.loading,
        message: state.modal.message,
        keep: state.user.keep,
        mortgageRate: state.mortgage.mortgageRate,
        equivalentAmount: state.mortgage.equivalentAmount,
        usable: state.mortgage.usable,
        loan: state.mortgage.loan,
        remaining: state.mortgage.remaining,
        usableXPA: state.mortgage.usableXPA,
        price: state.mortgage.price
    };
};

const mapDispatchToProps = (dispatch) =>
{
    return {
        actions: {
            bookActions: bindActionCreators(bookActions, dispatch),
            userActions: bindActionCreators(userActions, dispatch),
            modalActions: bindActionCreators(modalActions, dispatch),
            mortgageActions: bindActionCreators(mortgageActions, dispatch)
        }
    };
};

@connectFirebase
class Offset extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};
    }

    componentDidMount()
    {
        this.props.actions.mortgageActions.changeMortgagePrice();
    }

    renderUser(modalControl, toastOpen)
    {
        const { user, account, actions, tokenList } = this.props;
        if (user.wallet)
        {
            return (
                <MortgageUser
                    actions={actions}
                    account={account}
                    modalControl={modalControl}
                    toastOpen={toastOpen}
                    tokenList={tokenList}
                />
            );
        }
        else
        {
            return (
                <Login
                    wallet={user.wallet || {}}
                    actions={actions}
                    tokenList={tokenList}
                    modalControl={modalControl}
                    toastOpen={toastOpen}
                />
            );
        }
    }

    render()
    {
        const {
            user,
            tokenList,
            account,
            t,
            actions,
            mortgageRate,
            equivalentAmount,
            usable,
            loan,
            remaining,
            usableXPA,
            price,
        } = this.props;
        return (
            <WithPopup
                actions={this.props.actions}
                loading={this.props.loading}
                tokenList={this.props.tokenList}
                keep={this.props.keep}
                message={this.props.message}
            >
                {(modal, toastOpen) => (
                    <div className="co_mortgage">
                        <Header />
                        <nav>
                            <span><Link to="/exchange">{t('mortgage.index')}</Link></span>
                            <img src="/img/icon/ic-breadthumb.svg" alt="arrow" />
                            <span><Link to="/mortgage">{t('mortgage.mortgage')}</Link></span>
                            <img src="/img/icon/ic-breadthumb.svg" alt="arrow" />
                            <span>{t('mortgage.offset')}</span>
                        </nav>
                        <main>
                            <div>
                                <div className="title">{t('mortgage.wallet')}</div>
                                {this.renderUser(modal, toastOpen)}
                            </div>
                            <div>
                                <div className="title order">
                                    <span className="offset">
                                        {t('mortgage.offset')}
                                        <Prompt message={t('prompt.offset')} />
                                    </span>
                                    <span><Link to="/mortgage">{t('mortgage.mortgage')}</Link></span>
                                </div>
                                <OffsetOrder
                                    user={user}
                                    tokenList={tokenList}
                                    token="XPA"
                                    cash="USX"
                                    account={account}
                                    actions={actions}
                                    mortgageRate={mortgageRate}
                                    equivalentAmount={equivalentAmount}
                                    usable={usable}
                                    loan={loan}
                                    remaining={remaining}
                                    usableXPA={usableXPA}
                                    price={price}
                                    modalControl={modal}
                                    toastOpen={toastOpen}
                                />
                            </div>
                        </main>
                        <Footer />
                    </div>
                )}
            </WithPopup>
        );
    }
}

Offset.defaultProps = {
    t: () => {},
};

Offset.propTypes = {
    user: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    tokenList: PropTypes.array.isRequired,
    account: PropTypes.array.isRequired,
    t: PropTypes.func,
    loading: PropTypes.bool.isRequired,
    message: PropTypes.string.isRequired,
    keep: PropTypes.bool.isRequired,
    mortgageRate: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    equivalentAmount: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    usable: PropTypes.object.isRequired,
    loan: PropTypes.object.isRequired,
    remaining: PropTypes.object.isRequired,
    usableXPA: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    price: PropTypes.object.isRequired
};

export default connect(mapStateToProps, mapDispatchToProps)(translate('account', { wait: true })(Offset));
