import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import equal from 'deep-equal';

import { loginIO } from '../../utils/login';
import Prompt from '../prompt/prompt';
import './login.scss';
import * as rpc from '../../utils/rpc';

class Login extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {};

        this.importWallet = this.importWallet.bind(this);
        this.createWallet = this.createWallet.bind(this);
        this.observe = this.observe.bind(this);
    }

    shouldComponentUpdate(nextProps, nextState)
    {
        if (equal(nextProps, this.props) && equal(nextState, this.state))
        {
            return false;
        }
        else
        {
            return true;
        }
    }
    createWallet()
    {
        this.props.modalControl.open('create', { message: this.props.t('notDownloadWallet') });
    }

    importWallet()
    {
        this.props.modalControl.open('import');
    }

    observe()
    {
        // this.props.modalControl.open('observer');
        const _login = (address) => {
            loginIO({
                address
            }, '', 'metamask', '',  this.props.actions)
        };

        rpc.startExtension((address) => {
            _login(address)
        });
        rpc.setExtensionAddress((address) => {
            _login(address)
        });

    }

    render()
    {
        const { t } = this.props;
        return (
            <div className="c_login">
                <ul>
                    <li className="button" onClick={this.importWallet}><span>{t('importWallet')}</span></li>
                    <li className="button" onClick={this.createWallet}><span>{t('createWallet')}</span></li>
                    {/* <li className="unique" onClick={this.observe}>
                        <span>TideWallet Connect</span>
                        <Prompt type={2} message={t('prompt.obsever')} />
                        <i className="fa fa-angle-right" aria-hidden="true" />
                    </li> */}
                    <li className="unique" onClick={this.observe}>
                        <span>MetaMask</span>
                        <Prompt type={2} message={t('prompt.obsever')} />
                        <i className="fa fa-angle-right" aria-hidden="true" />
                    </li>
                </ul>
            </div>
        );
    }
}

Login.defaultProps = {
    t: () => {},
};

Login.propTypes = {
    t: PropTypes.func,
    modalControl: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
};

export default translate('account', { wait: true })(Login);

