import React from 'react';
import PropTypes from 'prop-types';
import './prompt.scss';

const Prompt = (props) =>
{
    return (
        <div className="c_prompt">
            <img src={`/img/icon/ic-help${props.type === 1 ? '-gray' : ''}.svg`} alt="question mark" />
            <div>
                {props.message}
            </div>
        </div>
    );
};

Prompt.defaultProps = {
    type: 1
};

Prompt.propTypes = {
    type: PropTypes.number,
    message: PropTypes.string.isRequired
};

export default Prompt;
