import * as types from '../constants/actionTypeMortgage';
import {
    getMortgageRate,
    getEquvialentAmount,
    getAmount,
    getUsableXPA,
    getMortgagePrice
    // getClosingLine
} from '../utils/rpc';

export const changeMortgageRate = (userAddress) =>
{
    return async (dispatch) =>
    {
        const data = await getMortgageRate(userAddress);
        dispatch({
            type: types.CHANGE_MORTGAGE_RATE,
            data
        });
    };
};

