import React from 'react';
import PropTypes from 'prop-types';
import Web3 from 'web3';
import { translate } from 'react-i18next';
import './mortgageUser.scss';
import { formatNumber } from '../../utils/format';

class MortgageUser extends React.Component
{
    constructor(props)
    {
        super(props);
        const tokens = Object.values(finalConfig.assetToken);
        tokens.unshift(finalConfig.XPA);
        tokens.unshift(finalConfig.ethAddress);
        this.state = {
            tokens
        };

        this.logout = this.logout.bind(this);
    }

    logout()
    {
        window.location.reload();
        // const { t, toastOpen, actions } = this.props;
        // const { setUser, updateMyBooks } = actions.userActions;
        // setUser({});
        // updateMyBooks([]);
        // clearTimeInterval();
        // actions.mortgageActions.mortgageLogout();
        // setTimeout(() =>
        // {
        //     toastOpen(t('logoutSuccess'));
        // }, 300);
    }

    render()
    {
        const { account, t, tokenList } = this.props;
        const BN = Web3.utils.BN;

        const list = account.map((l, i) =>
        {
            if (this.state.tokens.some(d => d === l.address))
            {
                const usable = new BN(l.usable, 16) / (10 ** tokenList.find(d => d.address).decimal);
                return (
                    <li key={l.address || i}>
                        <div>{l.token}</div>
                        <div>{formatNumber(10, usable)}</div>
                    </li>
                );
            }
            else
            {
                return null;
            }
        });

        return (
            <div className="c_mortgageUser">
                <div className="logout">
                    <div className="button" onClick={this.logout}>
                        <span>{t('logout')}</span>
                    </div>
                </div>
                {/* <button onClick={this.books}>get book</button> */}
                <h3>{t('balance')}</h3>
                <div>
                    <ul>
                        {list}
                    </ul>
                </div>
            </div>
        );
    }
}

MortgageUser.defaultProps = {
    t: () => {}
};

MortgageUser.propTypes = {
    account: PropTypes.array.isRequired,
    t: PropTypes.func,
    tokenList: PropTypes.array.isRequired
};

export default translate('account', { wait: true })(MortgageUser);
