import React from 'react';
import PropTypes from 'prop-types';
import update from 'immutability-helper';
import { translate } from 'react-i18next';
import ReactTooltip from 'react-tooltip';
import * as rpc from '../../utils/rpc';
import { formatNumber, tenFill, bnHex } from '../../utils/format';
import Prompt from '../prompt/prompt';
import './mortgageOrder.scss';

class MortgageData extends React.Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            HighestMortgageRate: '-',
            closingLine: '-',
        };

        this.renderAccount = this.renderAccount.bind(this);
    }

    componentDidMount()
    {
        const { user } = this.props;

        const getData = async () =>
        {
            const highest = await rpc.getHighestMortgageRate();
            const closingLine = await rpc.getClosingLine();

            if (user.wallet)
            {
                setTimeout(() =>
                {
                    this.props.getUserData(user.wallet.address);
                }, 200);
            }

            this.setState(update(this.state, {
                HighestMortgageRate: { $set: highest.replace('0x', '') },
                closingLine: { $set: closingLine.replace('0x', '') },
            }));
        };


        getData();
    }

    componentWillReceiveProps(nextProps)
    {
        if (nextProps.user.wallet && nextProps.user !== this.props.user)
        {
            setTimeout(() =>
            {
                this.props.getUserData(nextProps.user.wallet.address);
            }, 200);
        }
    }

    renderAccount()
    {
        const { loan, remaining, price } = this.props;

        const template = Object.keys(price).map((key) =>
        {
            return (
                <div key={key}>
                    <div>{key}</div>
                    <span data-tip data-for={`${key}price`}>{formatNumber(7, tenFill(bnHex(price[key]).toString(), -18))}</span>
                    <ReactTooltip id={`${key}price`} place="right">{tenFill(bnHex(price[key]).toString(), -18)}</ReactTooltip>
                    <span data-tip data-for={`${key}loan`}>{formatNumber(7, tenFill(bnHex(loan[key]).toString(), -18))}</span>
                    <ReactTooltip id={`${key}loan`} place="right">{tenFill(bnHex(loan[key]).toString(), -18)}</ReactTooltip>
                    <span data-tip data-for={`${key}remaining`}>{formatNumber(7, Number(tenFill(bnHex(remaining[key]).toString(), -18)))}</span>
                    <ReactTooltip id={`${key}remaining`} place="right">{tenFill(bnHex(remaining[key]).toString(), -18)}</ReactTooltip>
                </div>
            );
        });

        return template;
    }

    render()
    {
        const { HighestMortgageRate, closingLine } = this.state;
        const { mortgageRate, equivalentAmount, usableXPA, t, renderButton } = this.props;
        return (
            <div className="order-info">
                <div className="mortgage-rate divid">
                    <div className="infor">
                        <div className="rate">
                            <span>
                                {t('mortgage.mortgageRate')}
                                <Prompt message={t('prompt.mortgageRate')} />
                            </span>
                            <span data-tip data-for={`mortgage_rate${mortgageRate}`}>
                                {formatNumber(8, tenFill(bnHex(mortgageRate).toString(), -16)) || 0}%
                            </span>
                            <ReactTooltip id={`mortgage_rate${mortgageRate}`} place="right">{tenFill(bnHex(mortgageRate).toString(), -16)}</ReactTooltip>
                        </div>
                        <div>
                            <span>{t('mortgage.highestRate')}</span>
                            <span>
                                {formatNumber(8, tenFill(bnHex(HighestMortgageRate).toString(), -16)) || 0}%
                            </span>
                        </div>
                        <div>
                            <span>{t('mortgage.equival')}</span>
                            <span data-tip data-for={`equival${equivalentAmount}`}>{formatNumber(8, tenFill(bnHex(equivalentAmount).toString(), -18))} XPA</span>
                            <ReactTooltip id={`equival${equivalentAmount}`} place="right">{tenFill(bnHex(equivalentAmount).toString(), -18)}</ReactTooltip>
                        </div>
                        <div>
                            <span>{t('mortgage.detachable')}</span>
                            <span data-tip data-for={`usable${usableXPA}`}>
                                {Number(tenFill(bnHex(usableXPA).toString(), -18)) < (10 ** -6) ? 0 : formatNumber(8, tenFill(bnHex(usableXPA).toString(), -18)) || 0} XPA
                            </span>
                            <ReactTooltip id={`usable${usableXPA}`} place="right">{Number(tenFill(bnHex(usableXPA).toString(), -18)) < (10 ** -6) ? '0' : tenFill(bnHex(usableXPA).toString(), -18)}</ReactTooltip>
                        </div>
                    </div>
                    <div className="close-line">
                        <div>
                            <span>
                                {t('mortgage.closingLine')}
                                <Prompt message={t('prompt.closeLine')} />
                            </span>
                            <span>
                                {formatNumber(8, tenFill(bnHex(closingLine).toString(), -16)) || 0}%
                            </span>
                        </div>
                        <div className="rate">
                            <span>
                                {t('mortgage.mortgageRate')}
                            </span>
                            <span data-tip data-for={`mortgage_rate${mortgageRate}`}>
                                {formatNumber(8, tenFill(bnHex(mortgageRate).toString(), -16)) || 0}%
                            </span>
                            <ReactTooltip id={`mortgage_rate${mortgageRate}`} place="right">{tenFill(bnHex(mortgageRate).toString(), -16)}</ReactTooltip>
                        </div>
                    </div>
                </div>
                <div className="offset-btn divid">
                    {renderButton()}
                </div>
                <div className="mortgage-amount">
                    <div className="table divid">
                        <div className="t-header">
                            <span />
                            <span>{t('ratio')}</span>
                            {/* <span>{t('mortgage.usable')}</span> */}
                            <span>{t('mortgage.loan')}</span>
                            <span>{t('mortgage.remain')}</span>
                        </div>
                        {this.renderAccount()}
                    </div>
                </div>
            </div>
        );
    }
}

MortgageData.defaultProps = {
    t: () => {},
    renderButton: () => { return null; }
};

MortgageData.propTypes = {
    user: PropTypes.object.isRequired,
    mortgageRate: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    t: PropTypes.func,
    equivalentAmount: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    loan: PropTypes.object.isRequired,
    remaining: PropTypes.object.isRequired,
    usableXPA: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    price: PropTypes.object.isRequired,
    getUserData: PropTypes.func.isRequired,
    renderButton: PropTypes.func,
};

export default translate('account', { wait: true })(MortgageData);
