import React from 'react';
import PropTypes from 'prop-types';
import { translate } from 'react-i18next';
import * as rpc from '../../utils/rpc';
import MortgageData from './mortgageData';
import { bnHex, bnTen, tenFill } from '../../utils/format';

import './mortgageOrder.scss';

class OffsetOrder extends React.Component
{
    constructor(props)
    {
        super(props);

        this.doOffset = this.doOffset.bind(this);
        this.estimateOffset = this.estimateOffset.bind(this);
        this.getUserData = this.getUserData.bind(this);
        this.renderButton = this.renderButton.bind(this);
    }

    getUserData(userAddress)
    {
        const { actions } = this.props;
        actions.mortgageActions.changeMortgageRate(userAddress);
        actions.mortgageActions.changeEquivalentAmount(userAddress);
        actions.mortgageActions.changeUsableAmount(userAddress);
        actions.mortgageActions.changeLoanAmount(userAddress);
        actions.mortgageActions.changeRemainingAmount(userAddress);
        actions.mortgageActions.changeUsableXPA(userAddress);
    }

    async doOffset(tx)
    {
        const { user, actions, t, modalControl } = this.props;
        actions.modalActions.updateLoading(true);
        actions.modalActions.updateMessage(user.observer ? t('dataGenerating') : t('success'));

        const result = await rpc.doOffset(tx);
        if (result)
        {
            actions.modalActions.updateLoading(false);
            this.getUserData(user.wallet.address);
        }

        actions.modalActions.updateLoading(false);
        modalControl.close();
    }

    async estimateOffset(e)
    {
        const { loan, user, toastOpen, t, actions, equivalentAmount, price, account } = this.props;
        const type = e.currentTarget.dataset.type;
        const agent = '';
        if (user.observer) return toastOpen(`${t('mortgage.observerAlert')}${t('mortgage.offset')}`);

        if ((loan[type] > 0 && Number(equivalentAmount) <= 0) || !user.wallet) return toastOpen(t('mortgage.offsetMessage'));
        actions.modalActions.updateLoading(true);
        actions.modalActions.updateMessage(t('gettingTx'));

        const tx = await rpc.estimateOffset(user.wallet.address, user.key, type, agent);

        const v = bnTen(tenFill(bnHex(loan[type]).toString(), 18)).div(bnHex(price[type]));
        const equal = bnHex(equivalentAmount);

        const checkETH = (gas) =>
        {
            const eth = account.find(d => d.address === finalConfig.ethAddress);
            if (Number(tenFill(bnHex(eth.usable).toString(), -18)) < Number(gas))
            {
                return toastOpen(`ETH${t('out')}`);
            }
        };

        if (tx)
        {
            actions.modalActions.updateLoading(false);
            const gas = tenFill((tx.gasPrice * tx.gasLimit), -18);
            checkETH(gas);
            this.props.modalControl.open('offset', {
                toToken: { token: type },
                gas,
                fee: v.gt(equal) ? tenFill(equal.mul(bnHex(tx.fee)).toString(), -36) : tenFill(v.mul(bnHex(tx.fee)).toString(), -36),
                type: 'offset',
                exec: () =>
                {
                    this.doOffset(tx);
                }
            });
        }
        else
        {
            actions.modalActions.updateLoading(false);
        }
    }

    renderButton()
    {
        const { loan, equivalentAmount, t } = this.props;
        const template = Object.keys(loan).map((k) =>
        {
            return (
                <div className="item" key={k}>
                    <div className={(Number(`0x${loan[k]}`) > 0 && (bnHex(equivalentAmount).toString()) > 0) ? 'button' : 'button disable'} data-type={k} onClick={this.estimateOffset} >
                        <span>{t('mortgage.Offset')} {k}</span>
                    </div>
                </div>
            );
        });
        return template;
    }

    render()
    {
        const { mortgageRate, equivalentAmount, usable, loan, remaining, user, usableXPA, price, actions } = this.props;
        return (
            <div className="c_mortgageOrder offset">
                <MortgageData
                    mortgageRate={mortgageRate}
                    equivalentAmount={equivalentAmount}
                    loan={loan}
                    usable={usable}
                    remaining={remaining}
                    user={user}
                    usableXPA={usableXPA}
                    price={price}
                    getUserData={this.getUserData}
                    actions={actions}
                    renderButton={this.renderButton}
                />
            </div>
        );
    }
}

OffsetOrder.defaultProps = {
    t: () => {}
};

OffsetOrder.propTypes = {
    user: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
    t: PropTypes.func,
    mortgageRate: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    equivalentAmount: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    usable: PropTypes.object.isRequired,
    loan: PropTypes.object.isRequired,
    remaining: PropTypes.object.isRequired,
    usableXPA: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.number
    ]).isRequired,
    price: PropTypes.object.isRequired,
    modalControl: PropTypes.object.isRequired,
    account: PropTypes.array.isRequired
};

export default translate('account', { wait: true })(OffsetOrder);
